import React, {useEffect, useState} from "react";

import sleep from "../../utils/sleep";

const Discover = () => {
    const[showing, setShowing] = useState("");
    const[incr, setIncr] = useState(true);
    const fullMsg = "Discover";

    useEffect(() => {
        const tick = setInterval(async () => {
            if(incr) await sleep(120);
            const newShowing = fullMsg.substr(0, showing.length + (incr ? 1 : -1));
            setShowing(newShowing);
            if(newShowing === "" || newShowing === fullMsg){
                await sleep(newShowing === "" ? 480 : 960);
                setIncr(!incr);
            }
        }, 120);

        return () => clearInterval(tick);
    });

    let temp = [];
    for (let ch of showing){
        temp.push(<span key={ ch }>{ ch }</span>);
    }
    return <div id="discover">{ temp }<span id="discoverEnd">|</span></div>;
};

export default React.memo(Discover);