import React, {useRef, useState} from "react";
import useOnScreen from "../../utils/useOnScreen";

const Skill = () => {
    return (
        <>
            <SkillTitle/>
            <div id="skill">
                <PLanguage/>
                <PFrameWork/>
            </div>
        </>
    );
};

const SkillTitle = () => {
    const ref = useRef();
    const isVisible = useOnScreen(ref);
    const[anime, setAnime] = useState(false);
    // Only set once
    if(!anime && isVisible) setAnime(true);

    return (
        <div className={"innerTitle module" + (anime ? " anime" : "")} ref={ref}>Skill</div>
    );
}

const PLanguage = () => {
    const ref = useRef();
    const isVisible = useOnScreen(ref);
    const[anime, setAnime] = useState(false);
    // Only set once
    if(!anime && isVisible) setAnime(true);

    return (
        <div id="pLanguage" className={"card module" + (anime ? " anime" : "")} ref={ref}>
            <p className='heading'>Programming language</p>
            <div className="br">
            </div>
            <ul>
                <li>JavaScript</li>
                <li>TypeScript</li>
                <li>Java</li>
                <li>R</li>
                <li>Python</li>
                <li>GraphQL</li>
                <li>SQL</li>
            </ul>
        </div>
    );
};

const PFrameWork = () => {
    const ref = useRef();
    const isVisible = useOnScreen(ref);
    const[anime, setAnime] = useState(false);
    // Only set once
    if(!anime && isVisible) setAnime(true);

    return (
        <div id="pFramework" className={"card module" + (anime ? " anime2" : "")} ref={ref}>
            <p className='heading'>Frameworks</p>
            <div className="br">
            </div>
            <ul>
                <li>React.js</li>
                <li>Vue.js</li>
                <li>Apollo GraphQL</li>
                <li>Express.js</li>
                <li>Electron</li>
                <li>Java Spring</li>
                <li>D3.js</li>
            </ul>
        </div>
    );
};

export default React.memo(Skill);