import React, {useEffect, useState} from "react";
import {Routes, Route, useLocation} from "react-router-dom";

import Cover from "./pages/Cover";
import About from "./pages/About";
import Bg from "./pages/components/Bg";
import JumpToTop from "./pages/components/JumpToTop";
import Contact from "./pages/components/Contact";
import Nav from "./pages/components/Nav";
import Works from "./pages/Works";
import Notes from "./pages/Notes";
import NotePage from "./pages/components/NotePage";

import "./styles/App.css";
import "./styles/Footer.css";

function App() {
    const location = useLocation();
    const [onCover, setOnCover] = useState(location.pathname === '/');
    useEffect(
        () => {
            window.scrollTo(0, 0);
            if(onCover !== (location.pathname === '/')) setOnCover(!onCover)
        }, [onCover, location]
    );

    console.log("Hi, there !!! You find me !!!")
    return (
        <div className="App">
            <Bg/>
            <header>
                { onCover || <Nav/> }
            </header>

            <Routes>
                <Route path='/' element={<Cover/>}/>
                <Route path='about' element={<About/>}/>
                <Route path='works' element={<Works/>}/>
                <Route path='notes' element={<Notes/>}/>
                <Route path="/note:id" element={ <NotePage/> }/>
            </Routes>

            <footer>
                { onCover || <JumpToTop/> }
                { onCover || <Contact/> }
            </footer>
        </div>
    );
}

export default App;
