import React, {useRef, useState} from "react";
import useOnScreen from "../../utils/useOnScreen";

const Education = () => {
    return (
        <>
            <EducationTitle/>
            <ul id='education'>
                    <SingleCard time="Sep 2022 - Dec 2023 (in progress)" degree="Master of Computer Science" institution="University of California, Irvine" type=""/>
                    <SingleCard time="Jan 2019 - Dec 2021" degree="Bachelor of Science in Computer Information Science" institution="The Ohio State University" type="2"/>
            </ul>
        </>
    );
};

const EducationTitle = () => {
    const ref = useRef();
    const isVisible = useOnScreen(ref);
    const[anime, setAnime] = useState(false);
    // Only set once
    if(!anime && isVisible) setAnime(true);

    return (
        <div className={"innerTitle module" + (anime ? " anime" : "")} ref={ref}>Education</div>
    );
};

const SingleCard = ({time, degree, institution, type}) => {
    const ref = useRef();
    const isVisible = useOnScreen(ref);
    const[anime, setAnime] = useState(false);
    // Only set once
    if(!anime && isVisible) setAnime(true);

    return (
        <li className={"singleCard module" + (anime ? " anime" + type : "")} ref={ref}>
            <div className='time'>{ time }</div>
            <div className='degree'>{ degree }</div>
            <div className='institution'>{ institution }</div>
        </li>
    );
}

export default React.memo(Education);