import photo from "../../sources/photo.png";
import React, { useRef, useState } from "react";
import useOnScreen from "../../utils/useOnScreen";

const Bio = () => {
    return (
        <>
            <BioTitle/>
            <BioInner/>
        </>
    );
};

const BioTitle = () => {
    const ref = useRef();
    const isVisible = useOnScreen(ref);
    const[anime, setAnime] = useState(false);
    // Only set once
    if(!anime && isVisible) setAnime(true);

    return (
        <div className={"innerTitle module" + (anime ? " anime" : "")} ref={ref}>Bio</div>
    );
}

const BioInner = () => {
    const ref = useRef();
    const isVisible = useOnScreen(ref);
    const[anime, setAnime] = useState(false);
    // Only set once
    if(!anime && isVisible) setAnime(true);

    return (
        <div id='bio' className={"module" + (anime ? " anime" : "")} ref={ref}>
            <img src={ photo } alt=""/>
            <p>23-year-old software developer. I'm a new MCS student at the University of California, Irvine. Previously, I was an undergrad majoring in Computer Information Science, minoring in Statistics and Psychology at the Ohio State University. My interest lies in Human-Computer Interaction and Data Visualization.</p>
        </div>
    );
}

export default React.memo(Bio);