import React, { useRef, useState } from "react";
import useOnScreen from "../../utils/useOnScreen";

const WorkCard = ({ work : { id, name, description, img, link, repoLink } }) => {
    const ref = useRef();
    const isVisible = useOnScreen(ref);
    const[anime, setAnime] = useState(false);
    // Only set once
    if(!anime && isVisible) setAnime(true);

    return (
        <li className={"workCard module" + (anime ? " anime" + (Number(id) % 2 === 0 ? "" : "2") : "")} ref={ref}>
            <img src={ img } alt={ "Icon for " + name }/>

            <div className="details">
                <div className="workName">{ name }</div>
                <div className="workDescription">{ description }</div>
                <div className="workLinks">
                    { link && <a href={ link } target="_blank" rel="noreferrer">Link</a> }
                    { repoLink && <a href={ link } target="_blank" rel="noreferrer">Repository</a> }
                </div>
            </div>
        </li>
    );
}

export default React.memo(WorkCard);